import { dateI18n } from '@wordpress/date';
import { useEffect, useState } from '@wordpress/element';
import { sprintf, __ } from '@wordpress/i18n'; //Import __() from wp.i18n

const parseChangelog = (changelog, { hasBetas } = {}) =>
	changelog
		.replace(/\r/g, '')
		.replace(/(\r\n|\r|\n){3,}/g, '$1\n\n')
		.split('\n\n')
		.map((versionDescriptor) => {
			let [version, date] = versionDescriptor.split(/\r?\n/)[0].split(':')

			return {
				version,
				date: dateI18n('F j, Y', new Date(date.trim())),
				descriptor: versionDescriptor,
			}
		})
		.filter(({ version }) =>
			hasBetas ? true : version.indexOf('beta') === -1
		)
		.filter(({ version }) =>
			hasBetas ? true : version.split('.').length === 3
		)

const SingleVersion = ({ versionDescriptor }) => {
	const [_, ...allReleaseChanges] = versionDescriptor.descriptor.split(
		/\r?\n/
	)

	return (
        <div className="main--details">
		    <div className="rishi--versions">
			    <div className="version--title">
                    <h5>{sprintf(
                        // translators: placeholder here means the actual version.
                        __('Version: %s', 'rishi-companion'),
                        versionDescriptor.version
                    )}</h5>
                </div>
				<div className="released-date">
                    <p>{sprintf(
						// translators: placeholder here means the actual date.
						__('Released on %s', 'rishi-companion'),
						versionDescriptor.date
					)}</p>
				</div>
            </div>
			<div
				className="details"
				dangerouslySetInnerHTML={{
					__html: `<ul class="text-wrap"><li>

                        ${allReleaseChanges
							.join('\n')
							.trim()
							.split('\n')
							.map((c) => c.replace(/^-\s/, ''))
							.map((c) =>
								c.replace(/`(.*?)`/g, '<code>$1</code>')
							)
							.join('</li><li>')}
                        </li></ul>`

						.replace(
							/New Feature:/g,
							`<span class="letter light--green">N</span>`
						)
						.replace(
							/Fixes:/g,
							`<span class="letter light--yellow">F</span>`
						)
						.replace(
							/Update:/g,
							`<span class="letter light--blue">U</span>`
						),
				}}
			/>
        </div>
	)
}

function Changelog() {

    let changelog_cache = null
    const [isLoading, setIsLoading] = useState(!changelog_cache)
	const [changelog, setChangelog] = useState(changelog_cache)
	const [currentChangelog, setCurrentChangelog] = useState(0)

    const [toggleState, setToggleState] = useState(1);

    const toggleTab = (index) => {
        setToggleState(index);
    }

	let hasBetas = false

	const syncChangelog = async (verbose = false) => {
		if (verbose) {
			setIsLoading(true)
		}

		const body = new FormData()
        
        body.append('action', 'rc_get_latest_changelog')

		try {
			const response = await fetch(RishiCompanionDashboard.ajaxURL, {
				method: 'POST',
				body,
			})

			if (response.status === 200) {
				const { success, data } = await response.json()
				if (success && data.changelog) {
					setChangelog(data.changelog)
					changelog_cache = data.changelog
				}
			}
		} catch (e) {}

		setIsLoading(false)
	}

	useEffect(() => {
		syncChangelog(!changelog_cache)
	}, [])

    return (
        <>   
            <div className="changelog">   
                <div className="container">  
                    <div className="changelog--wrapper">   
                        <div className="button--wrapper">
                            <div className="rishi--btns">
                                <button className={toggleState === 1 ? "btn-tabs active-tabs" : "btn-tabs"}
                                    onClick={() => { toggleTab(1); setCurrentChangelog(0) } } >
                                    {__('Theme', 'rishi-companion')}
                                </button>
                                <button className="tabs"
                                    className={toggleState === 2 ? "btn-tabs active-tabs" : "btn-tabs"}
                                    onClick={() => { toggleTab(2); setCurrentChangelog(1) } } >
                                    {__('Companion', 'rishi-companion')}
                                </button>
                            </div>

                            <div className="logs">
                                <div>
                                    <span className="letter light--green">N</span>
                                    {__('New Feature','rishi-companion')}
                                </div>
                                <div>
                                    <span className="letter light--yellow">F</span>
                                    {__('Fixes','rishi-companion')}
                                </div>
                                <div>
                                    <span className="letter light--blue">U</span>
                                    {__('Update','rishi-companion')}
                                </div>
                            </div>    
                        </div>   
                        <div className="dr-whats-new-content">
                            <div className="content---wrapper content active-content">
                                    { changelog && changelog[currentChangelog].changelog
                                        ? parseChangelog(
                                                changelog[currentChangelog]
                                                    .changelog,

                                                { hasBetas }
                                        ).map((versionDescriptor) => (
                                                <SingleVersion
                                                    key={versionDescriptor.version}
                                                    versionDescriptor={
                                                        versionDescriptor
                                                    }
                                                />
                                        ))
                                        : __(
                                                'No changelog present at the moment.',
                                                'rishi-companion'
                                        )
                                    }
                            </div>
                            
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Changelog;
