import { useState } from '@wordpress/element'
import { __ } from "@wordpress/i18n";

const PluginBlock = ({ status, plugin, onPluginsSync }) => {
	const [isLoading, setIsLoading] = useState(false)

	const makeAction = async (plugin, actionName) => {
		const body = new FormData()

		body.append('plugin', plugin)
		body.append('action', actionName)

		setIsLoading(true)

		try {
			await fetch(RishiCompanionDashboard.ajaxURL, {
				method: 'POST',
				body
			})

			onPluginsSync()
		} catch (e) {}

		setIsLoading(false)
	}

	return (
		<div className="plugin--card--wrapper">
			<div className="plugin--details--wrapper">
				<h4 className="plugin--title">
					{plugin.title}

					{isLoading && (
						<svg width="15" height="15" viewBox="0 0 100 100">
							<g transform="translate(50,50)">
								<g transform="scale(1)">
									<circle cx="0" cy="0" r="50" fill="#687c93" />
									<circle
										cx="0"
										cy="-26"
										r="12"
										fill="#ffffff"
										transform="rotate(161.634)">
										<animateTransform
											attributeName="transform"
											type="rotate"
											calcMode="linear"
											values="0 0 0;360 0 0"
											keyTimes="0;1"
											dur="1s"
											begin="0s"
											repeatCount="indefinite"
										/>
									</circle>
								</g>
							</g>
						</svg>
					)}
				</h4>

				{plugin.description && (
					<div className="plugin--description">
						<p>{plugin.description}</p>
					</div>
				)}

				<div className="rc-extension-actions">
					{status === 'activated' && (
						<a
							onClick={() =>
								makeAction(plugin.name, 'rc_get_plugin_deactivate')
							}
							className="rc-button">
							{__('Deactivate', 'rishi-companion')}
						</a>
					)}

					{status === 'deactivated' && (
						<a
							onClick={() =>
								makeAction(plugin.name, 'rc_get_plugin_activate')
							}
							className="rc-button-primary">
							{__('Activate', 'rishi-companion')}
						</a>
					)}

					{status === 'uninstalled' &&
						!plugin.comingsoon &&
						plugin.type !== 'link' && (
							<a
								onClick={() =>
									makeAction(
										plugin.name,
										'rc_get_plugin_download'
									)
								}
								className="rc-button">
								{__('Install', 'rishi-companion')}
							</a>
						)}

					{status === 'uninstalled' &&
						!plugin.comingsoon &&
						plugin.type === 'link' && (
							<a
								href={plugin.link}
								className="rc-button"
								target="_blank">
								{__('Download', 'rishi-companion')}
							</a>
						)}

					{status === 'uninstalled' && plugin.comingsoon && (
						<span className="rc-badge">
							{__('COMING SOON', 'rishi-companion')}
						</span>
					)}
				</div>
			</div>
		</div>
	)
}

export default PluginBlock
