import {
    useEffect,
    Fragment,
    useState
} from '@wordpress/element'
import { __ } from "@wordpress/i18n";
import PluginBlock from './ui-components/PluginBlock';

let plugins_status_cache = null

let staticSource = RishiCompanionDashboard.pluginName

export const pluginsWithNames = () =>
    Object.values(staticSource).map((plugin, index) => {
        plugin['name'] = Object.keys(staticSource)[index]
        return plugin
    })

const RecommendedPlugins = () => {
    const [isLoading, setIsLoading] = useState(!plugins_status_cache)
    const [plugins_status, setPluginStatus] = useState(
        plugins_status_cache || []
    )

    const plugins = pluginsWithNames()

    const syncPlugins = async (verbose = false) => {
        if (verbose) {
            setIsLoading(true)
        }

        const body = new FormData()
        body.append('action', 'rc_get_plugins_status')

        try {
            const response = await fetch(RishiCompanionDashboard.ajaxURL, {
                method: 'POST',
                body
            })

            if (response.status === 200) {
                const { success, data } = await response.json()
                if (success) {
                    setPluginStatus(data)
                    plugins_status_cache = data
                }
            }
        } catch (e) {}

        setIsLoading(false)
    }

    useEffect(() => {
        syncPlugins(!plugins_status_cache)
    }, [])

    return (
        <div className="plugin--wrapper">
            <div className="container">
                
      
                    {plugins.length > 0 && (
                        <Fragment>
                            <div className="plugin--grid--wrapper">
                                {plugins.map(plugin => (
                                    <PluginBlock
                                        plugin={plugin}
                                        key={plugin.name}
                                        onPluginsSync={() =>
                                            syncPlugins()
                                        }
                                        status={
                                            (
                                                plugins_status.find(
                                                    ({ name }) =>
                                                        name ===
                                                        plugin.name
                                                ) || {}
                                            ).status
                                        }
                                    />
                                ))}
                            </div>
                        </Fragment>
                    )}
            </div>
        </div>
    )
}

export default RecommendedPlugins
